.card_container {
    width: 250px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    padding: 10px;
    color: black;
    text-decoration: none;
    position: relative;
}

.card_container:hover .Button {
    display: block;
}

.card_container img {
    padding: 10px;
    width: 100%;
    height: 100;
    object-fit: contain;
}

.Button {
    display: none;
    padding: 5px 10px;
    font-weight: 700;
    border: none;
    cursor: pointer;
    background-color: var(--primary-color);
    width: 100%;
    margin: 10px 0;
    border-radius: 30px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.Button :hover {
    background-color: var(--primary-shade);
}

.rating {
    display: flex;
    align-items: center;
    padding: 10px 0px;
}

.product_container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    max-width: 1200px; 
    margin: 100px auto; 
    gap: 50px;
    justify-content: center;
    pad: 0 20px;
}


.product_container section {
    margin: 0 auto;
}


.product_flexed {
    box-shadow: none;
    height: auto;
    display: flex;
    gap: 50px;
    width: 100%;
}

.product_flexed h3 {
    padding: 20px 0px;
}

.product_flexed .button {
    display: flex;
    position: static;
    width: 150px;
    margin-right: 40px;
}

.product_flexed {
    height: 50vh;
}

.product_flexed img {
    width: 300px;
    height: auto;
}

@media screen and (max-width:768px) {
    .product_flexed {
        display: flex;
        flex-direction: column;
    }
}