.fixed {
    position: sticky;
    top: 0;
    z-index: 100;
}

.header_container {
    background-color: black;
    display: flex;
    gap: 10px;
    align-items: center;
    color: white;
    height: 70px;
}

.logo_container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 250px;
    top: 30px;
}

.logo_container img {
    width: 80%;
    padding-top: 5px;
    margin-left: 20px;
    vertical-align: middle;
}

.delivery {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
    padding: 5px;
    font-weight: bold;
}

.delivery p {
    font-size: 10px;
}

.search {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    overflow: hidden;
    width: 100%;
    flex: 1;
}

.search select,
.search input {
    padding: 10px;
    font-weight: bold;
    outline: 1px solid white;
    
}

.search select {
    border: none;
}

.search input {
    border: 1px solid white;
    width: 100%;
    margin-right: -2px;
   
}

.search svg {
    padding: 7px;
    background: #febd69;
    color: black;
}

.search svg :hover {
    background-color: #d49644 ;
}

.search :focus-within {
    border: 2px solid rgb(red, green, blue);
}


.order_container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 450px;
    gap: 10px;
}

.order_container a {
    text-decoration: none;
    color: white;
    width: 50%;
}

.order_container p {
    font-size: 10px;
}


.language {
    width: 100px;
    display: flex;
    padding: 10px;
}

.language select {
    background-color: black;
    border: none;
    color: white;
}

.language img {
    width: 40%;
}

.cart {
    position: absolute;
    font-weight: bold;
    top: 15px;
    font-size: 20px;
    background-color: #1a1a1a;
    right: 16px;
    color: orange;
}

.order_container a :hover,
.delivery :hover,
.logo_container a :hover {
    border: 1px solid white;
    border-radius: 3px;
}

.lower_container ul li :hover {
    border: 1px solid white;
    border-radius: 3px;
}

.lower_container {
    background-color: black;
    color: white;
    height: 45px;

}

.lower_container ul {
    display: flex;
    list-style: none;
    gap: 5px;
}

.lower_container ul li {
    padding: 8px;
}

.lower_container ul li :first-child {
    display: flex;
    gap: 2px;
    padding-right: 5px;
    left: 0;
}

.menu_tag {
    display: flex;
    gap: 10px;
}


@media screen and (max-width: 576px) {
    .header_container {
        display: flex;
        flex-direction: column;
        height: auto;
    }

    .lower_container ul li:not(:first-child) {
        display: none;
    }

    .language {
        display: none;
    }
}


