.Order_outer_container {
    background-color: #efeeee;
    padding: 30px;
}

.Order_inner_container {
    background-color: #fff;
    padding: 20px;
}

.Order_inner_container h2 {
    border-bottom: 2px solid var(--primary-shade);
    padding: 20px;
}

.Order_inner_container p {
    padding: 12px;
}

.Order_inner_container img {
    width: 100px !important;
}

.Order_inner_container div {
    height: auto !important;
}