.payment_header {
    padding: 20px;
    text-align: center;
    font-size: 24px;
    background-color: lightgrey;
}

.payment_method {
    padding: 30px;
}

.flex {
    display: flex;
    gap: 20px;
    padding: 30px;

}

.flex h3 {
    min-width: 300px;
}

.flex img {
    width: 80px !important;
}

.flex div {
    height: auto !important;
}

.payment_card_container {
    max-width: 350px;
    width: 100%;
}

.payment_detailss {
    font-size: 16px;
}

/* >div- means direct children */
.payment__price >div {
    display: flex;
    font-size: 16px;
    gap: 10px;

}

.payment_detailss button {
    margin-top: 15px;
    background-color: var(--primary-color);
    border-radius: 10px;
    padding: 8px 100px;
    cursor: pointer;
}
.payment_detailss button {
    background-color: var(--primary-shade);
}


.loading {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
    color: gray ;
}

@media screen and (max-width: 768px) {
    .flex {
        flex-direction: column;
    }
}

